.footer,
.footer-for-home,
.special-group {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  position: fixed;
  background-color: #333333;
  color: white;
  padding: 1px;
  bottom: 0;
  width: 100vw;
}

/* just the icons */
.footer__icon {
  font-size: 2rem;
  margin-left: 1rem;
  color: white;
}

.search_me {
  margin-left: 1rem;
}

/* text and icons */
.footer__text__icon {
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer__text__icon :hover {
  opacity: 0.8;
}

.text-center {
  margin: 0;
}
